<template>
  <div id="evaluation-wrapper">
    <div class="query-box">
      <!-- 搜索固定标题行 -->
      <span class="search-title">查询条件</span>
      <div class="search-form">
        <Button type="success" size="small" @click="activitiesModal = true" v-model="activitiesValue"  v-html="activitiesContent"></Button>
        <i-select v-model.trim="city" style="width: 120px;margin: 0 30px 0 20px" placeholder="请选择市" @on-change="searchCity" size="small">
          <i-option v-for="item in cityList" :value="item.value">{{ item.label }}</i-option>
        </i-select>
        <Button type="primary" size="small" icon="ios-search" @click="handleAudit">查询条件</Button>
      </div>
    </div>
    <div class="content_box">
      <div id="map"></div>
      <div class="table-wrapper">
        <p>评估分值：60</p>
        <div style="height: calc(100% - 66px);overflow: auto;">
          <Table ref="table1" border stripe :loading="loading1" :columns="columns1" :data="data1"  size="small" highlight-row :max-height="tableHeight1"></Table>
        </div>
        <p style="text-align: center;">
          <Button type="primary" style="margin-right: 10px">重新计算</Button>
          <Button type="primary" @click="addActive = true">查看详情</Button>
        </p>
        <!-- 查看干扰评价详情-->
        <div class="seeDetails" :class="{addActive:addActive}">
          <evaluationDetails ref="evaluationDetails" @signOut="signOut"></evaluationDetails>
        </div>
      </div>
    </div>
    <!-- 人类活动列表-->
    <Modal
      v-model="activitiesModal"
      title="人类活动列表"
      @on-cancel="handleCancel">
      <div>
        <Table border stripe :loading="aLoading" :columns="aColumns1" :data="aData1" highlight-row :height="aTableHeight" ref="activitiesMultipleTable"
               @on-selection-change="handleSelectionChange"></Table>
        <div class="page_box" style="width: 100%;padding: 15px;text-align: right">
          <Page :total="aTotal" :page-size="aPageSize" :current.sync="aPageNum" @on-change="aPageChange" show-total/>
        </div>
      </div>
      <div slot="footer">
        <i-button type="text" @click="handleCancel" class="mr10">取消</i-button>
        <Button type="primary" @click="selectActivities">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getRemoteList, getAudit, getActiveLists } from '@/api/remoteSensing'
import { getMenu } from '@/api/acquisitionTable'
import evaluationDetails from './evaluationDetails'
export default {
  name: 'evaluationList',
  components: {
    // 查看详情
    evaluationDetails
  },
  data () {
    return {
      city: '',
      cityList: [],
      /* 加载 */
      loading1: false,
      /* 表格 */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '一级类型',
          align: 'center',
          key: 'title',
          render: (h, params) => {
            if (params.row.type === 1) {
              return h('div', params.row.name)
            }
          }
        },
        {
          title: '二级类型',
          align: 'center',
          key: 'title',
          render: (h, params) => {
            if (params.row.type === 2) {
              return h('div', params.row.name)
            }
          }
        },
        {
          title: '三级类型',
          align: 'center',
          key: 'name',
          render: (h, params) => {
            if (params.row.type === 3) {
              return h('div', params.row.name)
            }
          }
        },
        {
          title: '面积',
          align: 'center',
          key: 'area',
          // render: (h, params) => {
          //   this.data1[params.index].knowledge.forEach(item => {
          //     let res = item.children
          //     return h('ul', res.map(i => {
          //       return h('li', i.knowledgeName)
          //     }))
          //   })
          // }
        },
        {
          title: '变化量',
          align: 'center',
          key: 'num',
          // render: (h, params) => {
          //   this.data1[params.index].knowledge.forEach(item => {
          //     let res = item.children
          //     return h('ul', res.map(i => {
          //       return h('li', i.knowledgeName)
          //     }))
          //   })
          // }
        }
      ],
      data1: [],
      /* 表格动态高度 */
      tableHeight1: window.innerHeight - 290,
      /* 查看详情弹框状态 */
      addActive: false,
      /*
      * 人类活动
      * */
      // 选中人类活动id
      activitiesValue: '',
      // 选中人类活动name
      activitiesContent: '请选择人类活动',
      // 人类活动列表弹窗
      activitiesModal: false,
      /* 表格高度 */
      aTableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      aColumns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.aPageNum - 1) * this.aPageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '底图服务',
          align: 'center',
          width: 300,
          key: 'url'
        }
      ],
      aData1: [],
      /* 总条数 */
      aTotal: 0,
      aPageNum: 1,
      aPageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      aLoading: false,
      // 人类活动多选相关参数
      selectActiveRow: [],
      multipleActiveSelection: [],
      // 农业用地
      farmArr: [],
      // 建设用地
      ConArr: [],
      // 农业用地
      otherArr: [],
    }
  },
  watch: {
    // 人类活动多选
    multipleActiveSelection (data) { // 存储选中的row
      this.selectActiveRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectActiveRow.push(item.id)
        })
      }
    },
    taskIdArr (val) {
      this.taskParamsArr = val
    }
  },
  created () {},
  mounted () {
    this.getCodeMenu()
    this.getTableList()
    this.ActiveLists()
  },
  methods: {
    searchCity (val) {
      console.log(val)
    },
    /* axios: 获取省市县菜单 */
    getCodeMenu () {
      getMenu({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.cityList = []
          res.data.data[0].children.forEach(element => {
            this.cityList.push({
              label: element.label,
              value: element.comCode
            })
          })
          this.cityList.unshift({
            label: '全部',
            value: ''
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 获取人类活动列表数据 */
    getTableList () {
      this.aLoading = true
      getRemoteList({
        data: {
          pageSize: this.aPageSize,
          pageNum: this.aPageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.aData1 = res.data.data.rows
          this.aTotal = res.data.data.total
          this.aLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 人类活动列表分页跳转 */
    aPageChange (pageNum) {
      this.aPageNum = pageNum
      this.getTableList()
    },
    // 保存勾选人类活动
    selectActivities () {
      if (this.multipleActiveSelection.length < 1) {
        this.$Notice.error({ title: '人类活动不能为空！' })
      } else if (this.multipleActiveSelection.length > 1) {
        this.$Notice.error({ title: '只能选择一个人类活动！' })
      } else {
        this.activitiesContent = this.multipleActiveSelection[0].name
        this.activitiesValue = this.multipleActiveSelection[0].id
        this.handleCancel()
      }
    },
    // 勾选人类活动checkbox事件
    handleSelectionChange (val) {
      this.multipleActiveSelection = val
    },
    /* 取消选择人类活动 */
    handleCancel () {
      this.activitiesModal = false
      this.selectActiveRow = []
      this.multipleActiveSelection = []
    },
    /* 获取人类活动树形列表 */
    ActiveLists () {
      this.loading1 = true
      getActiveLists({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          console.log(res.data.data)
          this.data1 = res.data.data
          this.loading1 = false
          res.data.data.forEach(item => {
            if (item.type === 1) {
              this.farmArr.push(item)
            }
            if (item.type === 2) {
              this.ConArr.push(item)
            }
            if (item.type === 3) {
              this.otherArr.push(item)
            }
          })

          console.log(this.otherArr)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取审核详情 */
    handleAudit () {
      this.loading1 = true
      getAudit({
        data: {
          id: String(this.activitiesValue)
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          console.log(res.data.data)
          this.loading1 = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #evaluation-wrapper{
    .query-box{
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      overflow: hidden;
      .search-title {
        float: left;
        font-size: 14px;
        &:before {
          content: '';
          display: inline-block;
          width: 18px;
          height: 18px;
          background: url("../../../assets/redLine/sprite.png") 0 -63px no-repeat;
          background-size: auto;
          vertical-align: middle;
          margin-right: 8px;
        }
      }
      .search-form {
        float: right;
      }
    }
    .content_box {
      padding: 0 15px 15px;
      width: 100%;
      height: calc(100% - 65px);
      #map {
        float: left;
        width: 40%;
        height: 100%;
        border: 1px solid #000;
      }
      .table-wrapper {
        float: right;
        width: calc(60% - 15px);
        height: 100%;
        p {
          margin-bottom: 5px;
          font-size: 16px;
          text-align: left;
        }
        &>div {
          margin-bottom: 15px;
          table {
            width: 100%;
            border: 1px solid #ccc;
            border-collapse: collapse;
          }
          thead th{
            text-align: center;
            background-color: #fafafa;
          }
          th,td {
            padding-left: 20px;
            border: 1px solid #e9eaec;
            line-height: 30px;
          }
        }
      }
      .page_box{
        width: 100%;
        padding: 15px 0 0;
        text-align: right
      }
    }
    .seeDetails {
      margin: 0 !important;
      height: calc(100% - 80px);
      width: calc(60% - 30px);
      position: absolute;
      right: -120%;
      top: 65px;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right: 15px;
    }
  }
</style>
